import React from 'react';
import './App.css';
import List from './List';

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      items: [],
      curItem: {
        text: '',
        id: '',
      }
    }
    this.handleInput = this.handleInput.bind(this);
    this.addItem = this.addItem.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
  }
  handleInput = (e) => {
    this.setState({
      curItem: {
        text: e.target.value,
        id: Date.now()
      }
    })
  }
  addItem = (e) => {
    e.preventDefault();
    const item = this.state.curItem;
    if (item.text !== '') {
      let newList = [...this.state.items, item];
      this.setState({
        items: newList,
        curItem: {
          text: '',
          id: ''
        }
      })
    }
  }
  handleDelete = (key) => {
    console.log(key)
    const filterdItems = this.state.items.filter(item => item.id !== key)
    this.setState({
      items: filterdItems
    })
  }
  render() {
    return (
      <div className="App">
        <header>
          <form id="to-do-form" onSubmit={this.addItem}>
            <input type="text" placeholder="Enter text" onChange={this.handleInput} value={this.state.curItem.text} />
            <button type="submit">Add</button>
          </form>
          {console.log(this.state.items)}
        </header>
        <List items={this.state.items} handleDelete={this.handleDelete} />
      </div>
    );
  }
}

export default App;
